@import "/src/Sass/abstracts.scss";

.skills {
  &__container {
    display: grid;
    grid-template-columns: 0.1fr 1fr 0.1fr;
    column-gap: 1rem;
    align-items: center;
    @include respond-max(850px) {
      grid-template-columns: 1px 1fr 1px;
      column-gap: 1rem;
    }
  }
}
